import 'babel-polyfill'
import Vue from 'vue'
import vueSmoothScroll from 'vue-smooth-scroll'
import LikeButton from '../components/LikeButton.vue'
import TopButton from '../components/TopButton.vue'

Vue.use(vueSmoothScroll)

document.addEventListener('turbolinks:load', () => {
    new Vue({
        el: '#app',
        components: { LikeButton, TopButton }
    })

    //スクロール量を取得する関数
    function getScrolled() {
        return (window.pageYOffset !== undefined) ? window.pageYOffset : document.documentElement.scrollTop
    }

    //トップに戻るボタンの要素を取得
    const topBtn = document.querySelector('.top-btn')

    //ボタンの表示・非表示
    window.onscroll = function () {
        (getScrolled() > 200) ? topBtn.classList.add('fade-in') : topBtn.classList.remove('fade-in');
    }

    var clickable = document.querySelector('.ripple');

    /* mousedownだと直ぐに発動し、clickだとマウスボタンを離した時に発動する */
    clickable.on('mousedown', function (e) {
        var _self = this;
        var x = e.offsetX;
        var y = e.offsetY;

        var effect = _self.find('.ripple__effect');
        var w = effect.width();
        var h = effect.height();

        /* クリックした座標を中心とする */
        effect.css({
            left: x - w / 2,
            top: y - h / 2
        });

        /* jsではclassの付け替えをするだけ */
        if (!effect.hasClass('is-show')) {
            effect.addClass('is-show')
            /*
             * エフェクトアニメーションが終わったらclassを削除する
             * ここでは、単純にcssで設定するdurationと時間を合わせているだけですが
             * keyframes終了のイベント(AnimationEnd)が取れるかと思うので、それで対応した方が良いかも
             */
            setTimeout(function () {
                effect.removeClass('is-show')
            }, 750)
        }
        return false
    })
})

